"use client";
import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { Search, ExternalLink, MoreVertical, Store } from "lucide-react";

export default function ManageStoresPage() {
  const [stores, setStores] = useState<any[]>([]);
  const [search, setSearch] = useState("");

  useEffect(() => {
    async function fetchStores() {
      const { data } = await supabase
        .from('stores')
        .select('*')
        .order('created_at', { ascending: false });
      if (data) setStores(data);
    }
    fetchStores();
  }, []);

  const filteredStores = stores.filter(s => 
    s.name.toLowerCase().includes(search.toLowerCase()) || 
    s.slug.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-end">
        <div>
          <h2 className="text-3xl font-black text-white">Manage Vendors</h2>
          <p className="text-gray-400">Control and monitor all business entities on StoreLink.</p>
        </div>
        
        <div className="relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-500" size={18} />
          <input 
            type="text"
            placeholder="Search stores..."
            className="bg-gray-800 border border-gray-700 rounded-xl py-2 pl-10 pr-4 text-white outline-none focus:border-emerald-500 transition w-64"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
        </div>
      </div>

      <div className="bg-gray-800/50 border border-gray-700 rounded-3xl overflow-hidden">
        <table className="w-full text-left">
          <thead className="bg-gray-900/50 text-gray-400 text-xs uppercase font-bold">
            <tr>
              <th className="px-6 py-4">Store Details</th>
              <th className="px-6 py-4">Plan</th>
              <th className="px-6 py-4">Status</th>
              <th className="px-6 py-4">Created</th>
              <th className="px-6 py-4">Actions</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-700/50">
            {filteredStores.map((store) => (
              <tr key={store.id} className="hover:bg-white/5 transition">
                <td className="px-6 py-4 flex items-center gap-3">
                  <div className="w-10 h-10 bg-emerald-500/10 rounded-lg flex items-center justify-center text-emerald-500">
                    <Store size={20} />
                  </div>
                  <div>
                    <p className="font-bold text-white">{store.name}</p>
                    <p className="text-xs text-gray-500">/{store.slug}</p>
                  </div>
                </td>
                <td className="px-6 py-4">
                   <span className={`px-3 py-1 rounded-full text-[10px] font-black uppercase ${
                     store.subscription_plan === 'diamond' ? 'bg-yellow-500/10 text-yellow-500' :
                     store.subscription_plan === 'premium' ? 'bg-blue-500/10 text-blue-500' :
                     'bg-gray-500/10 text-gray-400'
                   }`}>
                     {store.subscription_plan}
                   </span>
                </td>
                <td className="px-6 py-4">
                  <div className="flex items-center gap-2">
                    <div className="w-2 h-2 rounded-full bg-emerald-500 animate-pulse" />
                    <span className="text-sm text-gray-300">Live</span>
                  </div>
                </td>
                <td className="px-6 py-4 text-sm text-gray-400">
                  {new Date(store.created_at).toLocaleDateString()}
                </td>
                <td className="px-6 py-4">
                  <div className="flex items-center gap-3">
                    <a href={`/${store.slug}`} target="_blank" className="p-2 hover:bg-gray-700 rounded-lg transition text-gray-400 hover:text-white">
                      <ExternalLink size={18} />
                    </a>
                    <button className="p-2 hover:bg-gray-700 rounded-lg transition text-gray-400 hover:text-white">
                      <MoreVertical size={18} />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}